@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(36, 36, 36);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
  position: fixed;
  width: 100%;
  min-height: 50px;
  height: 10vh;
  background-color: rgb(26, 26, 26);
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.logo {
  cursor: pointer;
}

.active {
  border-bottom: 2px solid rgba(108, 213, 255, 0.9);
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  cursor: pointer;
}

.navbar li a {
  position: relative;
  padding: 16px 0 5px;
  margin: 0 25px;
}

.nav-items {
  list-style: none;
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.toggle-button {
  position: absolute;
  flex-direction: column;
  justify-content: space-between;
  display: none;
  right: 2rem;
  width: 30px;
  height: 21px;
}

.toggle-button .bar {
  height: 3px;
  width: 100%;
  background-color: white;
  border-radius: 10px;
}

.mobile-navbar {
  position: fixed;
  display: none;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  min-height: 160px;
  height: 160px;
  top: 10vh;
  background-color: rgb(26, 26, 26);
}

.mobile-nav-items {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.mobile-nav-items li {
  list-style-type: none;
  color: white;
  padding: 10px;
}

.mobile-nav-items li a {
  cursor: pointer;
}

@media (max-width: 648px) {
  .toggle-button {
    display: flex;
  }

  .nav-items {
    display: none;
  }

  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .logo {
    margin-left: 2rem;
  }

  .mobile-active {
    display: flex;
  }
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-contents {
  width: 100%;
  height: 100vh;

  background: linear-gradient(
    103.68deg,
    rgba(26, 26, 26, 0.8) 0%,
    rgba(26, 26, 26, 0.2) 100%
  );

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.home-title {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 50%;
}

.title {
  font-size: 5em;
  color: rgb(255, 255, 255);
}

.subtitle {
  font-size: 3em;
  color: rgb(212, 212, 212);
}

button {
  display: inline-block;
  padding: 1em 5em;
  border-radius: 20px;
  border: none;
  text-decoration: none;
  font-weight: 300;
  font-size: 1em;
  color: #ffffff;
  background-color: #4eb5f1;
  box-shadow: 7px 8px 7px 2px rgba(0, 0, 0, 0.459);
  text-align: center;
  transition: all 0.2s;
}

button:hover {
  background-color: #4095c6;
}

@media all and (max-width: 850px) {
  button {
    display: block;
    margin: 0.2em auto;
    font-size: 0.5em;
  }

  .title {
    font-size: 2em;
  }

  .subtitle {
    font-size: 0.5em;
  }

  .home-title {
    width: 70%;
  }
}

@media all and (max-width: 1400px) {
  .home-title {
    width: 70%;
  }
}

.about-contents {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: linear-gradient(
    103.68deg,
    rgba(26, 26, 26, 0.8) 0%,
    rgba(26, 26, 26, 0.2) 100%
  );
}

.about-title {
  color: white;
  font-size: 3em;
}

.services-contents {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}

.services-title {
  color: black;
  font-size: 3em;
}

.contact-contents {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    103.68deg,
    rgba(26, 26, 26, 0.8) 0%,
    rgba(26, 26, 26, 0.2) 100%
  );
}

.contact-title {
  color: white;
  font-size: 3em;
}

.footer {
  width: 100%;
  height: 50vh;

  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: column;

  background-color: black;
  color: white;
}

.footer-top {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 80%;
}

.footer-brand {
  width: 35%;
  height: 100px;
}

.footer-company {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 30%;
  height: 100px;
}

.footer-company p {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 20px;
}

.footer-company ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.footer-company ul li {
  cursor: pointer;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 35%;
  height: 100px;
}

.socials-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}

.socials-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

.footer-bottom {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 20%;
  width: 50%;
  background-color: black;
  margin: auto;
}

.footer-bottom span {
  font-size: 12px;
}

@media (max-width: 648px) {
  .footer-brand {
    font-size: 10px;
  }

  .footer-company p {
    font-size: 10px;
  }

  .footer-company li {
    font-size: 10px;
  }

  .footer-bottom {
    width: 100%;
  }

  .socials-title {
    font-size: 10px;
  }

  .footer-bottom span {
    font-size: 8px;
  }

  .social-svg {
    height: 25px !important;
    width: 25px !important;
  }

  .socials-container {
    align-items: center;
  }
}

